.underlined {
  position: relative;
}
.underlined:before {
    content: ' ';
    background-color: #F2F2F2;
    height: 1px;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 2;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
    -webkit-animation: fillOut .3s;
            animation: fillOut .3s;
    -webkit-animation-timing-function: cubic-bezier(0.9, 0.07, 0.08, 0.935);
            animation-timing-function: cubic-bezier(0.9, 0.07, 0.08, 0.935);
}
@-webkit-keyframes fillIn {
0% {
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
}
to {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
}
}
@keyframes fillIn {
0% {
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
}
to {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
}
}
@-webkit-keyframes fillOut {
0% {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
}
to {
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
}
}
@keyframes fillOut {
0% {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
}
to {
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
}
}
::-webkit-scrollbar {
  width: 0px;
