.js-cursor-dim .js-cursor-underline {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  -webkit-transition: all 0.3s cubic-bezier(0, 0.45, 0.4, 0.99);
  transition: all 0.3s cubic-bezier(0, 0.45, 0.4, 0.99);
  z-index: 2;
}
.js-cursor-dim .js-cursor-underline:hover .underlined:before {
    -webkit-animation: fillIn .3s;
            animation: fillIn .3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
.js-cursor-dim .js-cursor-underline.selected .underlined:before {
    width: 100%;
}
.js-cursor-dim:hover .js-cursor-highlight {
  opacity: 0.45;
}
.js-cursor-dim .js-cursor-highlight:hover {
  opacity: 1;
}
.js-cursor-dim.active .js-cursor-underline:before {
  opacity: 1;
}

